*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  background: #fff;
  font-family: 'Helvetica Neue', Helvetica, 'Microsoft YaHei', FreeSans, Arimo,
    'Droid Sans', 'wenquanyi micro hei', 'Hiragino Sans GB',
    'Hiragino Sans GB W3', Arial, sans-serif;
  font-weight: normal;
  color: #555;
  font-size: 14px;
}

h2,
h3,
h4 {
  font-weight: normal;
}

input[type='text'],
input[type='email'],
input[type='tel'] {
  border: 1px solid #a7a7a7;
  height: 46px;
  padding: 0 20px;
  color: #a7a7a7;
  display: block;
  width: 100%;
  outline: none;
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}

input[type='text']:focus,
input[type='email']:focus,
input[type='tel']:focus {
  border-color: #999;
}

.btn {
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 3px;
  border: 1px solid #1d1d1f;
  text-align: center;
  text-decoration: none;
  outline: none;
  background-color: #1d1d1f;
  color: #fff;
}

.btn[disabled] {
  border-color: #aaa;
  color: #aaa;
}

a.btn {
  line-height: 48px;
}



